<script>
	import { getContext } from 'svelte';

	const { data, xGet, yScale, yDomain } = getContext('LayerCake');

	export let selected = null;
	export let hovered = null;

	let label = false;
	
	let min = $yDomain[0];
	let max = $yDomain[1];
</script>

<style>
	.markers-selected {
		pointer-events: none;
	}
</style>

<defs>
	<filter x="0" y="0" width="1" height="1" id="solid">
		<feFlood flood-color="#ffffffdd" result="bg" />
		<feMerge>
			<feMergeNode in="bg"/>
			<feMergeNode in="SourceGraphic"/>
		</feMerge>
	</filter>
</defs>
<g class="markers" on:mouseleave="{() => {hovered = null; label = false}}">
{#each $data as d}
	<line
		x1="{$xGet(d)}"
		x2="{$xGet(d)}"
		y1="{$yScale(max)}"
		y2="{$yScale(min)}"
		stroke="grey"
		stroke-width="0.5"
		stroke-linecap="butt"
	/>
	<line
		x1="{$xGet(d)}"
		x2="{$xGet(d)}"
		y1="{$yScale(max)}"
		y2="{$yScale(min)}"
		stroke="#ffffff00"
		stroke-width="6"
		stroke-linecap="butt"
		on:mouseenter="{() => {hovered = d.label, label = true}}"
		on:click="{() => selected = d.label}"
	/>
{/each}
</g>

{#if selected}
{#each $data.filter(d => d.label == selected) as hov}
<g class="markers-selected">
	<line
		x1="{$xGet(hov)}"
		x2="{$xGet(hov)}"
		y1="{$yScale(max)}"
		y2="{$yScale(min)}"
		stroke="black"
		stroke-width="2"
		stroke-linecap="butt"
	/>
</g>
{/each}
{/if}

{#if hovered}
{#each $data.filter(d => d.label == hovered) as hov}
<g class="markers-selected">
	<line
		x1="{$xGet(hov)}"
		x2="{$xGet(hov)}"
		y1="{$yScale(max)}"
		y2="{$yScale(min)}"
		stroke="orange"
		stroke-width="2"
		stroke-linecap="butt"
	/>
	{#if label}
	<text
	  x="{$xGet(hov) + 10}"
		y="{$yScale((min + max) / 2) + 2}"
		text-anchor="start"
		dominant-baseline="middle"
		filter="url(#solid)"
	>
		{hov.label}
	</text>
	{/if}
</g>
{/each}
{/if}